.bracket-podium{        
    height: 100%;
    min-height: 100vh;
    background-color: #333;
    background-image: url('https://acegif.com/wp-content/uploads/gif/confetti-2.gif');
    background-repeat: no-repeat;
    background-size: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}
.bracket-podium .podium-table{
    color:white;
    background: -webkit-linear-gradient(top, #3c3c3c 0%, #222222 100%);
    border-radius: 100px;
    min-width: 25rem;
    width: auto;
    padding: 4rem;    
    box-shadow: 20px 20px 10px black, 
        -20px -20px 10px black;        
}
.bracket-podium .tournamentName{
    font-size: 3rem;
}
.bracket-podium .podium-table .placement:not(:last-child){
    border-bottom: 2px solid hsla(0, 0%, 0%, 20%);
}
.bracket-podium .podium-table .sound{
    display: flex;
    justify-content: center;
    align-items: center;
}
.bracket-podium .podium-table img{
    max-width: 56px;
    margin-right: 1rem;
}
.bracket-podium .podium-table p{
    display: flex;
    align-items: center;
}
.bracket-podium .podium-table .placingNumber{
    font-size: 1.2rem;
    padding: 0.5rem 1rem;
    width: 2rem;
    box-shadow: 2px 2px 15px #222, 
        -2px -2px 15px #222;
    border: 0.2rem solid #444;
    border-radius: 10px;
    margin-top: 1rem;
    background-color: #333;
}
