.calculatorBody{
    align-items: center;
    justify-content: center;
    position: relative;    
    text-align: center; 
    color: white;
    padding-top: 2vh;
    background: -webkit-linear-gradient(top, #3c3c3c 0%, #222222 100%);
    background: linear-gradient(0deg, #3c3c3c 0%, #222222 100%);
    width: 100%;
    min-height: 91vh;
    height: auto;
}

.calculatorBody .teamBody{
    align-items: center;
    justify-content: center;
    position: relative;    
    color: white;
    text-align: center;
    padding: 2em 0em;
    width: 50%;
    display: inline-block;
}

.calculatorBody .teamBody h1{
    margin-bottom: 0.5em;
}

.calculatorBody .teamBody table{
    margin-top: 1em;
}

.calculatorBody .teamBody table, tr{
    width: 80%;
}

.calculatorBody .teamBody td:first-child{
    text-align: right;
}
.calculatorBody .teamBody td:last-child{
    text-align: left;
}

.calculatorBody .teamSummary{
    margin-top: 1em;

}

.calculatorBody .teamBody td span{
    margin-left: 1em;
}

