.calculatorBody .matchResults{
    display: flex;
    justify-content: center;    
    color:white;
}

.calculatorBody .matchResults table{
    width: 7rem;    
}

.calculatorBody .matchResults table, .matchResults h1, .matchResults, .bestOf, .matchResults td{
    margin-top: 0px;
    margin-bottom: 0px;
    padding-top: 0px;
    padding-bottom: 0px;
}


.calculatorBody .matchResults div{
    border-radius: 15px;
    background-color: #666666;
    position: relative;
    display: grid;
    width: 100%;
}

.calculatorBody .matchResults div span{
    margin: .3em;
    padding: 0;
    height: 2em;
    border-radius: 15px;
    width: 50%;    
    background-color: hsl(124, 100%, 15%);
    box-shadow: 0 0 2px hsla(124, 100%, 25%, 70%),
                0 0 4px hsla(124, 100%, 25%, 70%),
                0 0 6px hsla(124, 100%, 25%, 70%);
    transition: 1s all ease;
}

.calculatorBody .matchResults div span.checked{
    justify-self: right;
    right: 0;
    background-color: hsl(271, 100%, 15%);
    box-shadow: 0 0 2px hsla(271, 100%, 25%, 70%),
                0 0 4px hsla(271, 100%, 25%, 70%),
                0 0 6px hsla(271, 100%, 25%, 70%);
}

.calculatorBody .matchResults tr.hide{
    opacity: 30%;
}